body {
  background-color: #0e0517;
}
.wrap {
  margin-top: 100px;
  text-align: center;
}

button {
  border-radius: 100%;
  width: 100px;
  height: 100px;
  border-color: transparent;
  box-shadow: 0 9px #999;
  font-size: 35px;
  cursor: pointer;
  color: #fff;
}

.start__btn {
  background-color: #34f01f;
  margin-right: 20px;
  &:hover {
    background-color: #3e8e41;
  }
  &:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }
}

.stop__btn {
  background-color: #ff0015;
  &:hover {
    background-color: #a81824;
  }
  &:active {
    background-color: #a81824;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }
}

.button:hover {
  background-color: #3e8e41;
}

.button:active {
}
